.bold {
	font-weight: 600;
	font-size: 14px;
}
.row {
	margin: 0px;
}
#home {
	font-family: 'Open Sans', sans-serif;
	height: 80vh;
	width: 100%;
	background: #f6f6f6;
	display: block;
	@media only screen and (max-width: 600px) {
		height: 100%;
	}
	#background {
		position: fixed;
		top: 50%;
		left: 50%;
		min-width: 100%;
		min-height: 100%;
		width: auto;
		height: auto;
		z-index: -100;
		-webkit-transform: translateX(-50%) translateY(-50%);
		transform: translateX(-50%) translateY(-50%);
	}
	.banner {
		padding-top: 20px;
		padding-left: 5%;
		background-image: url(http://www.medicento.com/images/banner.png);
		p {
			font-size: 25px;
			color: #2c2b2b;
			font-weight: 800;
		}
		.help {
			font-weight: 500;
			font-size: 16px;
		}
		.nav_bottom {
			margin-top: 20px;
			.btn {
				color: #1a989e;
				font-weight: 600;
				font-size: 14px;
			}
			.active {
				border-bottom: 4px solid #1a989e;
			}
			.non-active {
				border-bottom: 4px solid transparent;
			}
		}
	}
	#image {
		margin-left: 10%;
	}
	.box {
		@media only screen and (max-width: 600px) {
			margin-top: 10px;
		}
		margin-top: -59px;
		margin-bottom: 60px;
		border-radius: 10px;
		background: white;
		width: 420px;
		float: right;
		margin-right: 10%;
		@media only screen and (max-width: 600px) {
			width: 90%;
			margin-left: 5%;
		}
		font-weight: 600;
		-webkit-box-shadow: 3px 3px 6px 1px rgba(115, 113, 115, 1);
		-moz-box-shadow: 3px 3px 6px 1px rgba(115, 113, 115, 1);
		box-shadow: 3px 3px 6px 1px rgba(115, 113, 115, 1);
		.partner {
			background-color: #1a989e;
			color: white;
			padding: 10px;
			border-top-left-radius: 10px;
			border-top-right-radius: 10px;
			font-weight: 700;
			font-size: 26px;
		}
		.box-body {
			padding: 20px;
		}
		.container_box {
			border-top-left-radius: 10px;
			border-top-right-radius: 10px;
			width: 90%;
			height: auto;
			margin: 0 auto;
			padding: 10px;
		}
		.btn {
			width: 32%;
			margin: 0.65%;
			background-color: grey;
			border: none;
			color: white;
			font-size: 16px;
			cursor: pointer;
		}
		.active {
			background: #ed1c24;
			border-radius: 8px;
		}
		.non-active {
			background-color: transparent;
			font-size: 16px;
			color: #555555;
		}
		input {
			margin: 20px;
			margin-left: 5%;
			width: 90%;
			padding-left: 10px;
			border: none;
			text-align: left;
			border-bottom: 1px solid grey;
		}
		.line {
			height: 1px;
			width: 100%;
			background: #c6c6c6;
		}
		.one_stop {
			color: #555555;
			font-size: 13px;
			text-align: center;
		}
		#phone {
			font-size: 15px;
			color: #676767;
			font-weight: 400;
			width: 90%;
			height: 40px;
			border-radius: 4px;
			background-color: #f0f0f0;
			border: 1px solid #cccccc;
			@media only screen and (max-width: 600px) {
				width: 90%;
				margin-left: 5%;
			}
		}
		#join_now {
			text-align: center;
			color: white;
			border-radius: 4px;
			width: 90%;
			height: 40px;
			font-weight: 600;
			border-radius: 4px;
			background-color: #ed1c24;
			@media only screen and (max-width: 600px) {
				width: 90%;
				margin-left: 5%;
			}
		}
		#join_now:hover {
			background: #2b2b2b;
			border-radius: 4px;
			color: #ffffff;
		}
		#state,
		#city {
			width: 44.5%;
			height: 40px;
			border-radius: 4px;
			background-color: #f0f0f0;
			border: 1px solid #cccccc;
			margin: 0px 0px;
			outline: none;
			@media only screen and (max-width: 600px) {
				width: 43%;
			}
		}
		#state {
			@media only screen and (max-width: 600px) {
				margin-left: 15px;
			}
			margin-left: 5%;
		}
		#city {
			@media only screen and (max-width: 600px) {
				margin-left: 15px;
			}
			margin-left: 1%;
		}
	}
}
.about {
	letter-spacing: 0.8px;
	font-family: 'Roboto Slab', serif;
	padding: 70px 30px;
	background: white;
	.line {
		height: 3px;
		background: #dedede;
		width: 28px;
		margin-top: 20px;
	}
	.head-about {
		font-size: 16px;
		line-height: 25px;
		letter-spacing: 0.5px;
		font-weight: 300;
		margin: 40px 15%;
		@media only screen and (max-width: 600px) {
			margin: 20px 5%;
		}
		text-align: justify;
	}
}
.services {
	background: #f6f6f6;
	padding: 20px 30px;
	display: block;
	h3 {
		margin-top: 20px;
		letter-spacing: 0.8px;
		font-family: 'Roboto Slab', serif;
	}
	.line {
		height: 3px;
		background: #dedede;
		width: 28px;
		margin-top: 20px;
	}
	.head-sm {
		font-size: 13px;
		letter-spacing: 1.5px;
		text-transform: uppercase;
		margin-top: 10px;
		margin-bottom: 20px;
		font-family: 'Roboto', sans-serif;
		font-weight: 700;
	}
	.text-grey {
		color: #434343;
		line-height: 26px;
		margin-bottom: 60px;
	}
}
.skill {
	background: white;
	padding: 50px 30px;
	display: block;
	h3 {
		margin-top: 20px;
		letter-spacing: 0.8px;
		font-family: 'Roboto Slab', serif;
	}
	.line {
		height: 3px;
		background: #dedede;
		width: 28px;
		margin-top: 20px;
	}
	.head-about {
		font-weight: 400;
		margin-top: 20px;
		line-height: 30px;
		font-size: 14px;
		letter-spacing: 0.4px;
	}
	.skillbar {
		padding: 10px;
	}
	.progress {
		background: #f6f6f6;
	}
	.progress-fill {
		width: 15%;
		background: #818181;
	}
}
.news {
	background: #f6f6f6;
	padding: 70px 30px;
	.line {
		height: 3px;
		background: #dedede;
		width: 28px;
		margin-top: 20px;
	}
	.box {
		margin-top: 20px;
		border: 1px solid black;
		margin-top: 50px;
		width: 100%;
		background: url(https://images.yourstory.com/cs/wordpress/2019/01/NAPP4669.jpg?fm=png&auto=format);
		background-repeat: no-repeat;
		background-size: cover;
		padding-top: 56%;
		border-radius: 20px;
		font-size: 14px;
		.box-body {
			background: #f9f7f7d9;
			border-radius: 20px;
		}
		.box-title {
			font-size: 16px;
			padding: 10px;
			color: black;
			a {
				color: black;
			}
		}
		p {
			padding-left: 10px;
			padding-right: 10px;
			color: black;
			margin-bottom: 0px;
		}
	}
	h3 {
		letter-spacing: 0.8px;
		font-family: 'Roboto Slab', serif;
	}
	.btn {
		background-color: grey;
		border: none;
		color: white;
		font-size: 15px;
		cursor: pointer;
		float: right;
	}
	.trending {
		padding: 10px;
		margin-top: 50px;
		border: 2px solid grey;
		border-radius: 20px;
		.row {
			font-size: 12px;
			margin-top: 10px;
			.date {
				padding-top: 10px;
				font-weight: 700;
				font-size: 10px;
			}
		}
	}
}
.app_link {
	background: white;
	padding: 70px 30px;
	h3 {
		margin-top: 20px;
		letter-spacing: 0.8px;
		font-family: 'Roboto Slab', serif;
	}
	.line {
		height: 3px;
		background: #dedede;
		width: 28px;
		margin-top: 20px;
	}
	.head-about {
		font-weight: 400;
		margin-top: 20px;
		line-height: 30px;
		font-size: 14px;
		letter-spacing: 0.4px;
	}
}
.testimonial {
	background-image: url(http://www.medicento.com/images/testimonial-bg.jpg);
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	background-size: cover;
	background-repeat: no-repeat;
	position: relative;
	padding: 0;
	margin: 0;
	width: 100%;
	padding: 70px 20px;
	h3 {
		margin-bottom: 20px;
		text-align: center;
		letter-spacing: 0.8px;
		font-family: 'Roboto Slab', serif;
	}
	.list-testimonial {
		-webkit-box-shadow: 6px 7px 5px 0px rgba(115, 113, 115, 1);
		-moz-box-shadow: 6px 7px 5px 0px rgba(115, 113, 115, 1);
		box-shadow: 6px 7px 5px 0px rgba(115, 113, 115, 1);
		letter-spacing: 0.8px;
		font-family: 'Roboto Slab', serif;
		background: white;
		border-radius: 10px;
		font-size: 16px;
		padding: 10px;
		h3 {
			font-size: 20px;
		}
	}
}
.team {
	background: #f6f6f6;
	padding: 20px 30px;
	display: block;
	.row {
		border: 1px solid rgb(34, 34, 34);
		border-radius: 20px;
		letter-spacing: 0.8px;
		.col-md-10 {
			h3 {
				font-family: 'Roboto Slab', serif;
				margin-top: 10px;
			}
			background: rgb(34, 34, 34);
			color: aliceblue;
			border-radius: 20px;
			a {
				color: rgba(255, 255, 255, 0.7);
				text-decoration: none;
				font-size: 15px;
			}
			p {
				padding-top: 5px;
				letter-spacing: 0.5px;
				font-size: 13px;
				line-height: 34px;
				font-weight: 300;
			}
		}
	}
	#about_us {
		background-color: grey;
		border: none;
		color: white;
		padding: 13px 16px;
		font-size: 15px;
		cursor: pointer;
		text-align: center;
		margin-top: 20px;
		margin-left: 40%;
	}
	#about_us:hover {
		background-color: RoyalBlue;
	}
}
.contact {
	padding: 70px 30px;
	letter-spacing: 0.8px;
	font-family: 'Roboto Slab', serif;
	background: white;
	#form {
		input,
		textarea {
			width: 100%;
			border: none;
			border-bottom: 1px solid black;
			padding: 5px;
			outline: none;
		}
		select {
			width: 100%;
			margin-top: 10px;
		}
		#sendMessage {
			text-align: center;
			color: white;
			margin: 15px;
			border-radius: 4px;
			padding: 10px;
		}
		#sendMessage:hover {
			background: #2b2b2b;
			border-radius: 4px;
			color: #ffffff;
		}
	}
	.line {
		height: 3px;
		background: #dedede;
		width: 28px;
		margin-top: 20px;
		margin-bottom: 20px;
	}
	.content-detail {
		font-weight: 400;
		line-height: 30px;
		font-size: 14px;
		letter-spacing: 0.4px;
	}
}
.footer {
	width: 100%;
	background: #2f2f2f;
	color: #636363;
	.row {
		padding: 0px 30px;
		p {
			etter-spacing: 0.5px;
			font-size: 13px;
			line-height: 34px;
			font-weight: 600;
			color: aliceblue;
			a {
				padding-right: 5px;
			}
			hr {
				margin-top: 20px;
				margin-bottom: 20px;
				border: 0;
				border-top: 1px solid #eee;
			}
			.footer-link {
				color: aliceblue;
				font-size: 12px;
			}
		}
	}
	.socials {
		width: 100%;
		padding: 0px;
		margin-bottom: 0px;
		text-align: center;
		.facebook {
			background: #969696;
		}
		.insta {
			background: #ababab;
		}
		.twitter {
			background: #d8d8d8;
		}
		.WhatsApp {
			background: #ababab;
		}
		.linkedin {
			background: #969696;
		}
		li {
			display: inline-block;
			list-style: outside none none;
			overflow: hidden;
			width: 20%;
			font-size: 13px;
			a {
				color: rgba(255, 255, 255, 0.7);
				position: relative;
				display: inline-block;
				letter-spacing: 1px;
				margin: 0;
				text-decoration: none;
				padding: 25px 0;
				width: 100%;
				font-size: 20px;
			}
		}
	}
}
